import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LocalStorageService {

  constructor() { }

  setUserData(userData) {
    localStorage.setItem('user', JSON.stringify(userData));
  }

  getUserData() {
    return JSON.parse(localStorage.getItem('user'));
  }

  clearLocalStorage() {
    localStorage.clear();
  }
}
